.search__panel {
  position: absolute;
  display: flex;
  width: 80%;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 1) 61%,
    rgba(0, 0, 0, 0.03) 100%
  );
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-bottom: 20px;
}

.search__panel input {
  width: 100%;
  height: 100%;
  border: 0px;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  padding: 15px 10px 0px 20px;
  font-size: 16px;
  color: #fff;
}

.search__panel ::-webkit-input-placeholder {
  color: #fff;
}
