header {
  position: fixed;
  display: flex;
  width: 100%;
  background: url("../../Images/nav_bar.png") bottom center no-repeat;
  background-size: cover;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding-bottom: 20px;
}

.hearder__leftsection {
  flex: 2;
  color: #fff;
  display: flex;
}

.back__arrow {
  padding: 0px 15px;
  display: flex;
  align-items: center;
}
.back__arrow img {
  height: 19px;
}

.header__search img {
  height: 25px;
}

.header__title {
  flex: 2;
  display: flex;
  justify-content: left;
  align-items: center;
  text-align: left;
  font-size: 20px;
  font-weight: lighter;
}
.header__search {
  padding: 15px;
}
